@import url(https://fonts.googleapis.com/css2?family=Cutive+Mono&display=swap);
body {
  margin: 0;

  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.app{background:url("https://dl.dropbox.com/s/mpmu0gjtxv2x3fs/Webp.net-resizeimage%20%281%29.jpg?raw=1") no-repeat center center fixed;background-size:cover;height:100%}.app .header{text-align:center;padding-top:50px}.app .header__title{font-size:48px;margin:0;color:#fff}.app .employees{margin-top:50px;display:grid;grid-template-columns:2,1fr;grid-gap:20px}@media screen and (min-width: 992px){.app .employees{grid-template-columns:repeat(5, 1fr);grid-gap:50px}}
.employee{background-color:rgba(255,255,255,.089);-webkit-backdrop-filter:blur(30px);backdrop-filter:blur(30px);width:150px;min-height:200px;padding:5px;text-align:center;margin:0 auto;border-radius:15px;border:2px solid rgba(189,189,189,.034);box-shadow:20px 20px 22px rgba(0,0,0,.3);color:#ffffffed;transition:.3s}.employee:hover{box-shadow:20px 20px 22px rgba(0,0,0,.5)}.employee__name{border-bottom:1px solid #ffffff91;padding-bottom:10px}.employee__role{font-style:italic;opacity:.8}
.counter{border:2px solid rgba(255,255,255,.226);margin:10px;border-radius:15px;padding:5px}.counter .far{font-size:16px;margin:10px;cursor:pointer;color:rgba(255,255,255,.746)}.counter .far:hover{color:rgba(132,148,167,.342)}
